import React from 'react';
import './Payment.scss';
import {Link} from 'react-router';
import Sidebar from '../../Sidebar/components/Sidebar.js';
import InviteFriends from '../../../Widgets/InviteFriends/components/InviteFriends.js';
import ReferFriends from '../../../Widgets/ReferFriends/components/ReferFriends.js';
import StripeUI from '../../../Widgets/StripeUI/components/StripeUI.js';
import PaymentCard from '../../../Widgets/PaymentCard/components/PaymentCard.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class Payment extends React.Component {
    constructor(props) {
        super(props);
    }

    onChange = () => {}

    goToIndex = () => {
        this.props.router.push('/');
    }

    render() {
        return (
          <div className="user-payment">
              <div className="header-section">
                  <div className="header-items">
                      <div className="header-left">
                          <img src={logoImg} className="droplet-logo" onClick={this.goToIndex}/>
                      </div>
                      <div className="header-right">
                          <label className="name-text clickable">Welcome back, Elson Ng</label>
                          <div className="slash-div"></div>
                          <label className="name-text clickable">Log out</label>

                          <img src={logOutImg} className="logout-img clickable"/>
                      </div>
                  </div>
              </div>

              <div className="container notification-container">
                  <div className="row">
                      <Sidebar />
                      <div className="col-10">
                      <div className="notification-section">
                          <div className="title-section">
                              <h4 className="title-text">Payment Method</h4>
                          </div>
                          <PaymentCard />
                      </div>
                      <div className="notification-section">
                          <div className="title-section">
                              <h4 className="title-text">Replace Existing Card With A New Card</h4>
                          </div>
                          <div className="detail-section">
                            <StripeUI />
                          </div>
                      </div>
                      <button className="btn btn-green btn-save">Update and Save</button>
                      </div>

                  </div>
              </div>
          </div>
        );
      }
    }

export default Payment;



