import React from 'react';
import './ReferFriends.scss';
import {Link} from 'react-router';

class ReferFriends extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
          <div className="refer-friends">
            <div className="notification-section">
                <div className="title-section">
                    <h4 className="title-text">Free Droplets</h4>
                </div>
                <div className="detail-section">
                    <div className="link-section text-left">
                        <div className="link-text">3 Droplets available to claim</div>
                    </div>
                    <h4>Refer more friends to unlock more free droplets.</h4>
                </div>
            </div>
          </div>
        );
    }
}


export default ReferFriends;



