import React from 'react';
import {Link} from 'react-router';
import './Sidebar.scss';

export class Sidebar extends React.Component{
  constructor(props){
    super(props);
  }

  render(){
    return(
      <div className="col-2">
          <button className="btn btn-white"><Link to='/user-buyDroplet'>Buy Droplet</Link></button>
          <button className="btn btn-white"><Link to='/user-promotions'>Invite Friends</Link></button>
          <div className="list-menu">
              <Link to='/droplet-history'>Droplet History</Link>
              <Link to='/user-payment'>Payment Method</Link>
              <Link to='/user-profile'>User Profile</Link>
          </div>
      </div>
    );
  }

}

export default Sidebar;



