import React from 'react';
import './StripeUI.scss';
import {Link} from 'react-router';

import visaImg from '../assets/visa.png';

class StripeUI extends React.Component {
    constructor(props) {
        super(props);
    }

    render(){
      return(
        <div className="stripe-ui">
          <div className="detail-section">
              <img src={visaImg} className="visa-img"/>
              <div className="form-group">
                  <label htmlFor="creditNumber">Credit Card Number</label>
                  <i className="fa fa-lock"></i>
                  <input type="text" className="form-control has-icon" id="creditNumber"/>

              </div>
              <div className="form-group">
                  <label htmlFor="creditName">Name on Credit Card</label>
                  <input type="text" className="form-control has-icon" id="creditName"/>
              </div>
              <div className="row">
                  <div className="col-3">
                      <div className="form-group">
                          <label htmlFor="selectMonth">Expiry Month</label>
                          <select className="form-control" id="selectMonth">
                              <option></option>
                              <option>1</option>
                              <option>2</option>
                              <option>3</option>
                              <option>4</option>
                              <option>5</option>
                              <option>6</option>
                              <option>7</option>
                              <option>8</option>
                              <option>9</option>
                              <option>10</option>
                              <option>11</option>
                              <option>12</option>
                          </select>
                      </div>
                  </div>
                  <div className="col-3">
                      <div className="form-group">
                          <label htmlFor="selectYear">Expiry Year</label>
                          <select className="form-control" id="selectYear">
                              <option></option>
                              <option>2018</option>
                              <option>2019</option>
                              <option>2020</option>
                          </select>
                      </div>
                  </div>
                  <div className="col-3">
                      <div className="form-group">
                          <label htmlFor="creditNumber">Security Code</label>
                          <i className="fa fa-question-circle"></i>
                          <input type="text" className="form-control has-icon" id="creditNumber"/>
                      </div>
                  </div>

              </div>

          </div>
          

        </div>
      );
    }

  }

export default StripeUI;



